import React, { useEffect, useReducer, useRef, useState } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import axios from 'axios';
import { toast } from 'react-toastify';
import { getError } from '../utils';
import { Helmet } from 'react-helmet-async';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Rating from '../components/Rating';
import LoadingBox from '../components/LoadingBox';
import MessageBox from '../components/MessageBox';
import Button from 'react-bootstrap/Button';
import Product from '../components/product';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleUp, faAngleDown } from '@fortawesome/free-solid-svg-icons';

import LinkContainer from 'react-router-bootstrap/LinkContainer';

const reducer = (state, action) => {
  switch (action.type) {
    case 'FETCH_REQUEST':
      return { ...state, loading: true };
    case 'FETCH_SUCCESS':
      return {
        ...state,
        products: action.payload.products,
        page: action.payload.page,
        pages: action.payload.pages,
        countProducts: action.payload.countProducts,
        loading: false,
      };
    case 'FETCH_FAIL':
      return { ...state, loading: false, error: action.payload };

    default:
      return state;
  }
};

const prices = [
  {
    name: '$1 to $50',
    value: '1-50',
  },
  {
    name: '$51 to $200',
    value: '51-200',
  },
  {
    name: '$201 to $1000',
    value: '201-1000',
  },
];

export const ratings = [
    {
    name: '4stars & up',
    rating: 4,
  },

  {
    name: '3stars & up',
    rating: 3,
  },

  {
    name: '2stars & up',
    rating: 2,
  },

  {
    name: '1stars & up',
    rating: 1,
  },
];

export default function SearchScreen() {
  const navigate = useNavigate();
  const { search } = useLocation();
  const sp = new URLSearchParams(search); // /search?category=Shirts
  const category = sp.get('category') || 'all';
  const sub_category = sp.get('sub_category') || 'all';
  const query = sp.get('query') || 'all';
  const price = sp.get('price') || 'all';
  const rating = sp.get('rating') || 'all';
  const order = sp.get('order') || 'newest';
  const brand = sp.get('brand') || 'all'
  const page = sp.get('page') || 1;

  const [showDepartment, setShowDepartment] = useState(false);
  const [showClothingType, setShowClothingType] = useState(false);
  const [showPrice, setShowPrice] = useState(false);
  const [showCustomerReview, setShowCustomerReview] = useState(false);
  
  const handleToggle = (setStateFunction) => {
    if (window.innerWidth <= 767) { // Check if the screen width is 767px or less (mobile view)
      setStateFunction(prevState => !prevState);
    }
    // For larger screens, do not toggle the state
  };

  const [showFilters, setShowFilters] = useState(false);
  const filterRef = useRef(null);

  useEffect(() => {
  const handleClickOutside = (event) => {
    if (filterRef.current && !filterRef.current.contains(event.target)) {
      setShowFilters(false);
    }
  };

  document.addEventListener('mousedown', handleClickOutside);

  return () => {
    document.removeEventListener('mousedown', handleClickOutside);
  };
}, [filterRef]);






  const [{ loading, error, products, pages, countProducts }, dispatch] =
    useReducer(reducer, {
      loading: true,
      error: '',
    });

  useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.get(
          `/api/products/search?page=${page}&query=${query}&category=${category}&sub_category=${sub_category}&price=${price}&rating=${rating}&order=${order}&brand=${brand}`
        );
        dispatch({ type: 'FETCH_SUCCESS', payload: data });
      } catch (err) {
        dispatch({
          type: 'FETCH_FAIL',
          payload: getError(error),
        });
      }
    };
    fetchData();
  }, [category, sub_category, error, order, page, price, query, rating,brand]);

  const [categories, setCategories] = useState([]);
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const { data } = await axios.get(`/api/products/categories`);
        setCategories(data);
      } catch (err) {
        toast.error(getError(err));
      }
    };
    fetchCategories();
  }, [dispatch]);

  // frontend
  const [subcategories, setsubCategories] = useState([]);
    useEffect(() => {
      const fetchsubCategories = async () => {
        try {
          const { data } = await axios.get(`/api/products/sub_categories`);
          setsubCategories(data);
        } catch (err) {
          toast.error(getError(err));
        }
      };
      fetchsubCategories();
    }, [dispatch]);



  const getFilterUrl = (filter) => {
    const filterPage = filter.page || page;
    const filterCategory = filter.category || category;
    const filterSubcategory = filter.sub_category || sub_category; 
    const filterQuery = filter.query || query;
    const filterRating = filter.rating || rating;
    const filterPrice = filter.price || price;
    const sortOrder = filter.order || order;
    const filterBrand = filter.brand || brand;
    return `/search?category=${filterCategory}&sub_category=${filterSubcategory}&query=${filterQuery}&price=${filterPrice}&rating=${filterRating}&brand=${filterBrand}&order=${sortOrder}&page=${filterPage}`;
};

  return (
    <div>
  
  <div className="wide-image-container">
    <img className="wide-image" src="../images/wide-test.png" />
  </div>  
    <div className="search-screen-container">
      
      <div className="filters-toggle" onClick={() => setShowFilters(!showFilters)}>
          Filter {showFilters ? '-' : '+'}
        </div>
      <div>
        <Helmet>
          <title>Search Products</title>
        </Helmet>
        <Row>
        <Col md={2} className={`filters ${showFilters ? 'show' : ''}`} ref={filterRef}>
          <h3 className="search-header" onClick={() =>handleToggle(setShowDepartment)}>
            Department <span className="toggle-arrow">
            <FontAwesomeIcon icon={showDepartment ? faAngleUp : faAngleDown} />
          </span>
          </h3>
            
            <div>
              {showDepartment && (
              <ul className="search-items">
                <li>
                  <Link
                    className={'all' === category ? 'text-bold' : ''}
                    to={getFilterUrl({ category: 'all' })}
                  >
                    Any
                  </Link>
                </li>
                {categories.map((c) => (
                  <li key={c}>
                    <Link
                      className={c === category ? 'text-bold' : ''}
                      to={getFilterUrl({ category: c })}
                    >
                      {c}
                    </Link>
                  </li>
                ))}
              </ul>
              
              )}
              <ul className="search-items desktop">
                <li>
                  <Link
                    className={'all' === category ? 'text-bold' : ''}
                    to={getFilterUrl({ category: 'all' })}
                  >
                    Any
                  </Link>
                </li>
                {categories.map((c) => (
                  <li key={c}>
                    <Link
                      className={c === category ? 'text-bold' : ''}
                      to={getFilterUrl({ category: c })}
                    >
                      {c}
                    </Link>
                  </li>
                ))}
              </ul>
            </div>
        {/* //////////    */}
            <h3 className="search-header" onClick={() => handleToggle(setShowClothingType)}>
            Clothing Type <span className="toggle-arrow">
            <FontAwesomeIcon icon={showClothingType? faAngleUp : faAngleDown} />
          </span>
          </h3>  
          <div>
            {showClothingType && (
            <ul className="search-items">
              <li>
                <Link
                  className={'all' === sub_category ? 'text-bold' : ''}
                  to={getFilterUrl({ sub_category: 'all' })}
                >
                  Any
                </Link>
                </li>
                {subcategories.map((subcat) => (
                  <li key={subcat}>
                    <Link
                      className={subcat === sub_category ? 'text-bold' : ''}
                      to={getFilterUrl({ sub_category: subcat })}
                    >
                {subcat}
                      </Link>
                    </li>
                  ))}
                </ul>
            )}
              <ul className="search-items desktop">
              <li>
                <Link
                  className={'all' === sub_category ? 'text-bold' : ''}
                  to={getFilterUrl({ sub_category: 'all' })}
                >
                  Any
                </Link>
                </li>
                {subcategories.map((subcat) => (
                  <li key={subcat}>
                    <Link
                      className={subcat === sub_category ? 'text-bold' : ''}
                      to={getFilterUrl({ sub_category: subcat })}
                    >
                {subcat}
                      </Link>
                    </li>
                  ))}
                </ul>
              </div>

            <div>
            <h3 className="search-header" onClick={() => handleToggle(setShowPrice)}>
              Price <span className="toggle-arrow">
            <FontAwesomeIcon icon={showPrice? faAngleUp : faAngleDown} />
          </span>
            </h3>
            {showPrice && (
              <ul className="search-items">
                <li>
                  <Link
                    className={'all' === price ? 'text-bold' : ''}
                    to={getFilterUrl({ price: 'all' })}
                  >
                    Any
                  </Link>
                </li>
                {prices.map((p) => (
                  <li key={p.value}>
                    <Link
                      to={getFilterUrl({ price: p.value })}
                      className={p.value === price ? 'text-bold' : ''}
                    >
                      {p.name}
                    </Link>
                  </li>
                ))}
              </ul>
            )}
              <ul className="search-items desktop">
                <li>
                  <Link
                    className={'all' === price ? 'text-bold' : ''}
                    to={getFilterUrl({ price: 'all' })}
                  >
                    Any
                  </Link>
                </li>
                {prices.map((p) => (
                  <li key={p.value}>
                    <Link
                      to={getFilterUrl({ price: p.value })}
                      className={p.value === price ? 'text-bold' : ''}
                    >
                      {p.name}
                    </Link>
                  </li>
                ))}
              </ul>
            </div>
            <div>
            <h3 className="search-header" onClick={() => handleToggle(setShowCustomerReview)}>
              Avg. Customer Review<span className="toggle-arrow">
            <FontAwesomeIcon icon={showCustomerReview ? faAngleUp : faAngleDown} />
          </span>
            </h3>
            {showCustomerReview && (
              <ul className="search-items">
                {ratings.map((r) => (
                  <li key={r.name}>
                    <Link
                      to={getFilterUrl({ rating: r.rating })}
                      className={`${r.rating}` === `${rating}` ? 'text-bold' : ''}
                    >
                      <Rating caption={' & up'} rating={r.rating}></Rating>
                    </Link>
                  </li>
                ))}
                <li>
                  <Link
                    to={getFilterUrl({ rating: 'all' })}
                    className={rating === 'all' ? 'text-bold' : ''}
                  >
                    <Rating caption={' & up'} rating={0}></Rating>
                  </Link>
                </li>
              </ul>
            )}
            <ul className="search-items desktop">
                {ratings.map((r) => (
                  <li key={r.name}>
                    <Link
                      to={getFilterUrl({ rating: r.rating })}
                      className={`${r.rating}` === `${rating}` ? 'text-bold' : ''}
                    >
                      <Rating caption={' & up'} rating={r.rating}></Rating>
                    </Link>
                  </li>
                ))}
                <li>
                  <Link
                    to={getFilterUrl({ rating: 'all' })}
                    className={rating === 'all' ? 'text-bold' : ''}
                  >
                    <Rating caption={' & up'} rating={0}></Rating>
                  </Link>
                </li>
              </ul>
            </div>
          </Col>
          <Col md={9}>
            {loading ? (
              <LoadingBox></LoadingBox>
            ) : error ? (
              <MessageBox variant="danger">{error}</MessageBox>
            ) : (
              <>
                <Row className="justify-content-between mb-3">
                  <Col  className= "results" md={6}>
                    <div>
                      {countProducts === 0 ? 'No' : countProducts} Results
                      {query !== 'all' && ' : ' + query}
                      {category !== 'all' && ' : ' + category}
                      {sub_category !== 'all' && ' : ' + sub_category}
                      {price !== 'all' && ' : Price ' + price}
                      {rating !== 'all' && ' : Rating ' + rating + ' & up'}
                      {brand !== 'all' && ` : Brand - ${brand}`}
                      {query !== 'all' ||
                      category !== 'all' ||
                      rating !== 'all' ||
                      
                      price !== 'all' ? (

                        <Button
                          variant="light"
                          onClick={() => navigate('/search')}
                        >
                          <i className="fas fa-times-circle"></i>
                        </Button>
                      ) : null}
                    </div>
                  </Col>
                  <Col className="text-end">
                    {' '}
                    <select
                      value={order}
                      onChange={(e) => {
                        navigate(getFilterUrl({ order: e.target.value }));
                      }}
                    >
                      <option value="newest">Newest Arrivals</option>
                      <option value="lowest">Price: Low to High</option>
                      <option value="highest">Price: High to Low</option>
                      <option value="toprated">Avg. Customer Reviews</option>
                    </select>
                  </Col>
                </Row>
                {products.length === 0 && (
                  <MessageBox>No Product Found</MessageBox>
                )}
              <div className="image-grid">
                <Row>
                  {products.map((product) => (
                    <Col sm={6} lg={4} className="mb-3" key={product._id}>
                      <Product product={product}></Product>
                    </Col>
                  ))}
                </Row>
                </div>

                <div>
                  {[...Array(pages).keys()].map((x) => (
                    <Link
                      key={x + 1}
                      className="mx-1"
                      to={getFilterUrl({ page: x + 1 })}
                    >
                      <Button
                        className={Number(page) === x + 1 ? 'text-bold' : ''}
                        variant="light"
                      >
                        {x + 1}
                      </Button>
                    </Link>
                  ))}
                </div>
              </>
            )}
          </Col>
        </Row>
      </div>
    </div>
    </div>
  );
}

